import React, { createContext, useState, useEffect } from "react";

const api = window.WP_API_Settings;
// const dev = { rootUrl: "http://settings.test/wp-json/", nonce: "4af01ed684" };

const { Provider, Consumer } = createContext();

// Then create a provider Component
function AppProvider(props) {
  const [open, setOpen] = useState(false);
  const [data, setData] = useState([]);

  useEffect(() => {
    getData();
  }, []);

  const toggleDrawer = () => {
    setOpen(!open);
  };

  /// INITIAL DATA
  const getData = async () => {
    try {
      await fetch(`${api?.rootUrl}progressnet/v1/data`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "X-WP-Nonce": api?.nonce
        }
      })
        .then(response =>
          response.json().then(res => ({
            status: response.status,
            body: res
          }))
        )
        .then(myJson => {
          if (myJson.status === 200) {
            setData(myJson.body);
          } else {
            setData([]);
            console.log(myJson);
          }
        });
    } catch (error) {
      console.log(error);
    }
  };

  const update = async newData => {
    console.log(newData);
    try {
      await fetch(`${api?.rootUrl}progressnet/v1/update`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "X-WP-Nonce": api?.nonce
        },
        body: JSON.stringify({ ...newData })
      })
        .then(response =>
          response.json().then(res => ({
            status: response.status,
            body: res
          }))
        )
        .then(myJson => {
          if (myJson.status === 200) {
            setData(myJson.body);
            console.log(myJson.body);
          } else {
            setData([]);
            console.log(myJson);
          }
        });
    } catch (error) {
      console.log(error);
    }
  };
  const newIssue = async newData => {
    console.log(newData);
    try {
      await fetch(`${api?.rootUrl}progressnet/v1/new`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "X-WP-Nonce": api?.nonce
        },
        body: JSON.stringify({ ...newData })
      })
        .then(response =>
          response.json().then(res => ({
            status: response.status,
            body: res
          }))
        )
        .then(myJson => {
          if (myJson.status === 200) {
            console.log(myJson.body);
            setData(myJson.body);
          } else {
            setData([]);
            console.log(myJson);
          }
        });
    } catch (error) {
      console.log(error);
    }
  };

  const deleteIssue = async newData => {
    console.log(newData);
    try {
      await fetch(`${api?.rootUrl}progressnet/v1/delete`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "X-WP-Nonce": api?.nonce
        },
        body: JSON.stringify({ ...newData })
      })
        .then(response =>
          response.json().then(res => ({
            status: response.status,
            body: res
          }))
        )
        .then(myJson => {
          if (myJson.status === 200) {
            console.log(myJson.body);
            setData(myJson.body);
          } else {
            setData([]);
            console.log(myJson);
          }
        });
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Provider
      value={{
        open,
        data,

        handleUpdate: newData => {
          update(newData);
        },
        handleNew: newData => {
          newIssue(newData);
        },
        handleDelete: newData => {
          deleteIssue(newData);
        },
        handleDrawer: () => {
          toggleDrawer();
        }
      }}
    >
      {props.children}
    </Provider>
  );
}

export { AppProvider };
export default Consumer;
